.index {
  padding-bottom: 50px;

  &-list {
    &-header {
      font-size: 17px;
      color: #333;
      padding: 2px 0 4px;
    }
    &-item-title {
      padding: 5px 0;
    }
    &-item-active {
      background-color: rgba($color: #999999, $alpha: 0.1);
    }
  }

  &-add {
    bottom: 160px;
    transform: rotate(45deg);
  }

  &-modal-input-box {
    margin: 10px 15px;
  }
}
